var server = window.location.hostname;
var port = "1337";
var status_request = document.getElementById("status_request");
var list_operation = document.getElementById("list_operation");
fetch("http://" + server + ":" + port + "/operations/")
    .then(function (response) {
    return response.json().then(function (data) { return ({ status: response.status, body: data }); });
}).then(function (data) {
    if (data.status == 200) {
        for (var _i = 0, _a = data.body; _i < _a.length; _i++) {
            var operation = _a[_i];
            list_operation.insertAdjacentHTML('beforeend', "<p>" + operation.id + ". " + operation.name_operation + "</p>");
        }
    }
    else if (data.status == 404) {
        status_request.innerHTML = "Error";
    }
});
